}
.navlinks>Button{
    font-size: 22px;
   border: none;
   padding: 10px 20px;
   font-weight: bolder;
   font-family: sans-serif;

  
}
.navlinks>Button:hover{
    cursor: pointer;
}

.content h1{
    font-size:90px;
    
  
   margin: 5px 50px;
}
.content>div:nth-child(2){
    display: flex;
    justify-content: center;
    align-items:flex-end;
    font-size: 22px;
}


.intro{
    width: 70%;
    display: flex;
    justify-content: space-between;
    float: right;
   
}
@media screen and (min-width: 801px) and (max-width:1200px){
    
    .content h1{
        font-size:70px;
        
      
        margin: 10px 30px;
    }

}

@media screen and (min-width: 380px) and (max-width:800px){
    .navlinks>Button{
        font-size: 18px;
        padding: 10px 10px;
    
      
    }
    .content h1{
        font-size:50px;
        
      
        margin: 5px 25px;
    }

}